@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: black;
  font-family: 'Montserrat', sans-serif;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #000;
}

.showcase {
    background-image: url(/static/media/showcase.0a38cc23.jpg);
    background-position: center ;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: auto;
    padding-top: 9rem;
    color: #000;
    text-transform: capitalize;
    text-align: center;
    font-size: 2rem;
}
.Header_header__1m625 {
    background-image: url(/static/media/showcase.0a38cc23.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 32rem;
}

.Header_showcase__ks4ut {
    /* background-color: black; */
    width: 100%;
    height: auto;
    margin: 0 auto;
    padding: 9rem 0 0 9rem;
    color: #000;
    text-transform: capitalize;
    text-align: left;
    font-size: 2rem;
    line-height: 1.3;
}   

@media (max-width: 1310px){
    .Header_showcase__ks4ut {
        text-align: center;
        padding: 5rem 0 0 0;
    }
}

@media (max-width: 820px){
    .Header_showcase__ks4ut {
        font-size: 2rem;
        text-align: center;
        padding: 5rem 0 0 0;

    }
}

@media (max-width: 560px){
    .Header_header__1m625 {
        height: 20rem;
    }
    .Header_showcase__ks4ut {
        font-size: 1.5rem;
        padding: 6rem 0 0 0;
        text-align: center;
    }
}

@media (max-width: 400px){
    .Header_showcase__ks4ut{
        font-size: 1rem;
        padding: 7rem 0 0 0;
    }
}
.Projects_projects__iFqiR {
    background-color: #FFFAFA;
    width: 100%;
    height: auto;
    padding-top: 3vh;
    margin: 0;
    text-align: center;
    color: #000;
}

.Projects_projects__iFqiR h2 {
    margin: 2vh;
    font-size: 2.8rem;
    text-transform: capitalize;
}

.Projects_listContainer__2FJAi {
    width: 90%;
    height: auto;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    text-align: left;
    align-items: center;
}

.Projects_listContainer__2FJAi h3 {
    font-size: 1.8rem;
    text-transform: capitalize;
    margin: 2vh;

}

@media (max-width: 1250px){
    .Projects_listContainer__2FJAi {
        width: 98%;
    }
}

@media (max-width: 1030px){
    .Projects_listContainer__2FJAi {
        text-align: center;
    }
}

@media (max-width: 550px){
    .Projects_projects__iFqiR h2, .Projects_listContainer__2FJAi h3 {
        font-size: 1.5rem;
    }
}

.ProjectsList_track__fCn5i {
  /* background-color: burlywood; */
  width: 100%;
  height: auto;
  margin: 1rem auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  border: none;
  border-radius: 5px;
}

.ProjectsList_projectCard__10rxU {
  background-color: #000;
  width: 25%;
  height: 32vh;
  margin: 1.5vh;
  padding: 0vh;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  border-radius: 20px;
  z-index: 1;
}

.ProjectsList_projectCard__10rxU img {
  width: 100%;
  height: 80%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  z-index: 0;
}

.ProjectsList_projectCard__10rxU h4 {
  text-align: center;
  text-transform: capitalize;
  font-size: 1.1rem;
  text-decoration: none;
  margin: 1vh auto;
}

.ProjectsList_projectCard__10rxU h4 a {
  color: #f9f9f6;
  /* color: #DDCC70; */
}

@media (max-width: 1030px) {
  .ProjectsList_track__fCn5i {
    width: 95%;
    /* flex-direction: column; */
    /* flex-wrap: nowrap; */
  }

  .ProjectsList_projectCard__10rxU {
    width: 25%;
    height: auto;
  }

  .ProjectsList_projectCard__10rxU h4 {
    font-size: 0.8rem;
  }
}

@media (max-width: 750px) {
  .ProjectsList_track__fCn5i {
    /* background-color: red; */
    width: 80%;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .ProjectsList_projectCard__10rxU {
    width: 100%;
  }

  .ProjectsList_projectCard__10rxU img {
    height: auto;
  }
  .ProjectsList_projectCard__10rxU h4 {
    font-size: 1.2rem;
  }
}

.Footer_footer__2YfMX {
  background-color: #000;
  color: #fff;
  width: 100%;
  height: auto;
  margin: 0 auto;
  padding: 3vh 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;

  font-size: 1.5rem;
  text-transform: capitalize;
}

.Footer_footer__2YfMX ul {
  width: 25%;
  height: 3rem;
  margin: 1vh auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}

.Footer_footer__2YfMX ul li {
  cursor: pointer;
}

.Footer_footer__2YfMX ul li a {
  color: #fff;
}

@media (max-width: 800px) {
  .Footer_footer__2YfMX,
  .Footer_footer__2YfMX ul {
    font-size: 1.2rem;
  }
}

