.track {
  /* background-color: burlywood; */
  width: 100%;
  height: auto;
  margin: 1rem auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  border: none;
  border-radius: 5px;
}

.projectCard {
  background-color: #000;
  width: 25%;
  height: 32vh;
  margin: 1.5vh;
  padding: 0vh;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  border-radius: 20px;
  z-index: 1;
}

.projectCard img {
  width: 100%;
  height: 80%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  z-index: 0;
}

.projectCard h4 {
  text-align: center;
  text-transform: capitalize;
  font-size: 1.1rem;
  text-decoration: none;
  margin: 1vh auto;
}

.projectCard h4 a {
  color: #f9f9f6;
  /* color: #DDCC70; */
}

@media (max-width: 1030px) {
  .track {
    width: 95%;
    /* flex-direction: column; */
    /* flex-wrap: nowrap; */
  }

  .projectCard {
    width: 25%;
    height: auto;
  }

  .projectCard h4 {
    font-size: 0.8rem;
  }
}

@media (max-width: 750px) {
  .track {
    /* background-color: red; */
    width: 80%;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .projectCard {
    width: 100%;
  }

  .projectCard img {
    height: auto;
  }
  .projectCard h4 {
    font-size: 1.2rem;
  }
}
